            </el-select>
          </div>

          <el-table :data="paginatedSchedule"
                    :default-sort="{prop: 'month', order: 'ascending'}"
                    border
                    style="width: 100%; margin-top: 15px;">
            <el-table-column
                label=""
                prop="month"
                sortable></el-table-column>
            <el-table-column
                label=""
                prop="date"
                sortable></el-table-column>
            <el-table-column
                label=""
